import React from "react";
import styles from "./navbar.module.css";
import { BlobBase } from "../../../constants";
import { useHover } from "../../hooks/useHover";

export type NavbarItemProps = {
  href?: string;
  imgSrc: string;
  currentPage: boolean;
  expanded: boolean;
  onClick?: () => void;
  title?: string;
  additional?: string;
  additionalLinkOne?:string;
  additionalLinkTwo?: string;
  additionalTitle?: string;
  targetExternalPage?: boolean;
};

const NavbarItem = (props: NavbarItemProps) => {
  const [hoverRef, isHovered] = useHover();
  let href = props.href ? { href: props.href } : {};

  return (
    <>
    {!props.additional &&  <a
      {...href}
      ref={hoverRef}
      className={`${styles.button} 
      ${props.expanded && styles.expanded} 
      ${isHovered && styles.notSelected} `}
      onClick={() => {
        if (props.onClick) {
          props.onClick();
        }
      }}
      target={`${props.targetExternalPage ? "_blank" : "_self"}`}
    >
      <div
        className={`${styles.navImg}
        ${props.currentPage && styles.selected}`}
      >
        <img src={`${BlobBase}${props.imgSrc}`} alt="" />
      </div>
        {props.title && !props.additional &&(
        <>
        
        <div
          className={`${styles.titleContainer} 
          ${isHovered && styles.buttonHover} 
          ${(props.expanded || isHovered) && styles.expanded}`}
        >
          <div
            className={`${styles.navbarMenuTitle}
          ${props.currentPage && styles.selected} `}
          >
            {props.title}
          </div>
        </div>

            </>)  
        }
        
    </a>}

    {props.additional &&  <span
      {...href}
      ref={hoverRef}
      className={`${styles.button} 
      ${props.expanded && styles.expanded} 
      ${isHovered && styles.notSelected} `}
      onClick={() => {
        if (props.onClick) {
          props.onClick();
        }
      }}
      style={{'display':'inline'}}
      // target={`${props.targetExternalPage ? "_blank" : "_self"}`}
    >
      <div
        className={`${styles.navImg}
        ${props.currentPage && styles.selected}`}
      >
        <img src={`${BlobBase}${props.imgSrc}`} alt="" />
      </div>
        {props.title && (
        <>
        
        <div
          className={`${styles.titleContainer} 
          ${isHovered && styles.buttonHover} 
          ${(props.expanded || isHovered) && styles.expanded}`}
        >
          <div
            className={`${styles.navbarMenuTitle}
          ${props.currentPage && styles.selected} `}
          >
             
          <a
            className={`${styles.button} ${props.expanded && styles.expanded} `}
            style={{ 'display': 'inline-block', 'color': 'white', 'textDecoration':'none !important'}} href={props.additionalLinkOne}>
                  <span style={{ 'textDecoration': 'none' }}>{props.additionalTitle} </span>&nbsp;- Embedded
               {/* <a 
                className={`${styles.button} ${props.expanded && styles.expanded} `}
                style={{ 'display': 'inline-block', 'color': 'white'}} target="_blank" href={props.additionalLinkTwo}>
                    New Tab]
              </a> */}
          </a>

          </div>
          
        </div>

            </>)  
        }
             
    </span>}



    
    </>
  );
};

export default NavbarItem;
