import React, { useState, useEffect } from "react";

import { PowerbiServiceInstance, EmbedType } from "../../powerbi.service";
import Header, { PageType } from "../features/header/header";
import styles from "./Factory.module.css";
import Navbar from "../features/navbar/navbar";
import FactoryPopup from "../features/factory-popup/factory-popup";
import SecureRedirect from "../features/secure-redirect/secure-redirect";
import { BlobBase } from "../../constants";

function Factory() {
  const { fc_reportId, fc_overview, fc_alert, fc_production, fc_oee, fc_downtime, fc_energyMaintenance } = window['runConfig'];

  const [showPopUp, setShowPopUp] = useState(false);
  const [dashboardLoading, setDashboardLoading] = useState(false);
  const [showFactoryMini, setShowFactoryMini] = useState(true);

  const reportId = fc_reportId;
  const overview = fc_overview;
  const alert = fc_alert
  const production = fc_production;
  const oee = fc_oee;
  const downtime = fc_downtime;
  const energyMaintenance = fc_energyMaintenance;

  const powerbiService = PowerbiServiceInstance;

  const width = 438;
  const widthCover = 440;
  const height = 248;
  const heightTall = 401;

  useEffect(() => {
    if (dashboardLoading) {
      return;
    }

    setDashboardLoading(true);
    try {
      powerbiService.load(reportId, {
        type: EmbedType.Report,
        elementId: production,
        height,
        width,
        pageName: production,
      });
      powerbiService.load(reportId, {
        type: EmbedType.Report,
        elementId: oee,
        height: heightTall,
        width: widthCover,
        pageName: oee,
      });
      powerbiService.load(reportId, {
        type: EmbedType.Report,
        elementId: energyMaintenance,
        height,
        width,
        pageName: energyMaintenance,
      });
      powerbiService.load(reportId, {
        type: EmbedType.Report,
        elementId: downtime,
        height: height,
        width,
        pageName: downtime,
      });
    } catch (error) {
      setDashboardLoading(false);
    }
  }, [dashboardLoading, powerbiService]);

  useEffect(() => {
    let refreshTimer = setInterval(() => {
      powerbiService.refresh(production);
      powerbiService.refresh(oee);
      powerbiService.refresh(energyMaintenance);
      powerbiService.refresh(downtime);
    }, 15000);
    return () => clearInterval(refreshTimer);
  });

  return (
    <div className="page-container">
      <SecureRedirect></SecureRedirect>
      <div id="scalable-wrapper" className="content-container">
        <div id="content" className="content">
          <Header type={PageType.Factory}></Header>
          <Navbar type={PageType.Factory} />
          <div className="factory-page">
            <a href="#/machine" className={styles.alertMachine}></a>
            <div
              className={styles.showMap}
              onClick={() => setShowPopUp(true)}
            ></div>
            <div className={styles.switchBlock}>
              <div
                className={`${styles.switchMiniMapBtn} ${
                  showFactoryMini ? styles.active : ""
                }`}
                onClick={() => setShowFactoryMini(true)}
              >
                <div className={styles.switchMiniMapIcon}>
                  <img src={`${BlobBase}icon_factoy_overview.png`} alt="" />
                </div>
                <div className={styles.switchMiniMapTooltip}>
                  Factory Alerts
                </div>
              </div>
              <div
                className={`${styles.switchMiniMapBtn} ${
                  !showFactoryMini ? styles.active : ""
                }`}
                onClick={() => setShowFactoryMini(false)}
              >
                <div className={styles.switchMiniMapIcon}>
                  <img src={`${BlobBase}icon_heatmap.png`} alt="" />
                </div>
                <div className={styles.switchMiniMapTooltip}>Heatmap</div>
              </div>
            </div>
            <img className="background" src={`${BlobBase}Factory.jpg`} alt="" />
            {showFactoryMini && (
              <img
                className={styles.factoryAnimation}
                src={`${BlobBase}factory_alerts_small.gif`}
                alt=""
              />
            )}
            {!showFactoryMini && (
              <div className={styles.videoContainer}>
                <video
                  playsInline
                  controls={false}
                  preload="auto"
                  src={BlobBase + "Heatmap_small.mp4"}
                  controlsList="nodownload"
                  className={styles.video}
                  autoPlay
                  muted
                >
                  <source src={BlobBase + "Heatmap_small.mp4"} />
                </video>
              </div>
            )}
            <div
              id={overview}
              className={styles.overview + " report show"}
              style={{
                width: width + "px",
                height: heightTall + "px",
              }}
            ></div>
            <div
              id={alert}
              className={styles.alert + " report  show"}
              style={{
                width: width + "px",
                height: height + "px",
              }}
            ></div>
            <div
              id={production}
              className={styles.production + " report  show"}
              style={{
                width: width + "px",
                height: height + "px",
              }}
            ></div>
            <div
              id={oee}
              className={styles.oee + " report  show"}
              style={{
                width: widthCover + "px",
                height: heightTall + "px",
              }}
            ></div>
            <div
              id={energyMaintenance}
              className={styles.energyMaintenance + " report  show"}
              style={{
                width: width + "px",
                height: height + "px",
              }}
            ></div>
            <div
              id={downtime}
              className={styles.downtime + " report  show"}
              style={{
                width: width + "px",
                height: height + "px",
              }}
            ></div>
          </div>
        </div>
      </div>
      {showPopUp && (
        <FactoryPopup
          onPopupDismiss={() => {
            setShowPopUp(false);
          }}
        ></FactoryPopup>
      )}
    </div>
  );
}

export default Factory;
