import React, { useState, useEffect } from "react";
import Header, { PageType } from "../features/header/header";
import styles from "./EmbeddedCampaignDashboard.module.css";
import { PowerbiServiceInstance, EmbedType } from "../../powerbi.service";
import Navbar from "../features/navbar/navbar";
import SecureRedirect from "../features/secure-redirect/secure-redirect";

function EmbeddedPostgreSqlDashboardAfter() {
  
  const {  PSSqlEmbeddedDashboardAfter_ID } = window['runConfig']
  const [dashboardLoading, setDashboardLoading] = useState(false);

  const reportid = PSSqlEmbeddedDashboardAfter_ID;
   const powerbiService = PowerbiServiceInstance;

  const width = 1920;
  const height = 1280;

  useEffect(() => {
    if (dashboardLoading) {
      return;
    }

    setDashboardLoading(true);
    try {
      powerbiService.load(reportid, {
        type: EmbedType.Report,
        elementId: reportid,
        height: height,
        width,
        pageName: ""
      });
    } catch (error) {
      setDashboardLoading(false);
    }
  }, [dashboardLoading, powerbiService]);
  return (
    <div className="page-container">
      <SecureRedirect></SecureRedirect>
      <Header type={PageType.PSEmbedded}></Header>
      <Navbar type={PageType.PSEmbedded} />
      <div
        id={reportid}
        className={styles.finance + " dashboard"}
        style={{
          width: "calc(100% - 48px)",
          height: "calc(100% - 54px)",
        }}
      ></div>
    </div>
  );
}

export default EmbeddedPostgreSqlDashboardAfter;
